import { post } from './pontoon';
import type { MetricCounts, MetricGauges } from './request-types';

export interface CountBatch {
  api?: string;
  apiKey?: string;
  headers?: Record<string, string>;
  counts: MetricCounts['counts'];
}

export interface GaugeBatch {
  api?: string;
  apiKey?: string;
  headers?: Record<string, string>;
  gauges: MetricGauges['gauges'];
}

export interface Batch<K> {
  [serverName: string]: {
    [serviceName: string]: K;
  };
}

export const BATCH_THROTTLE_MS = 5_000;

export function throttle(fn: () => void) {
  let timeout: ReturnType<typeof setTimeout> | null = null;
  return () => {
    if (timeout !== null) {
      return;
    }
    timeout = setTimeout(() => {
      timeout = null;
      fn();
    }, BATCH_THROTTLE_MS);
  };
}

export const batches: {
  count: Batch<CountBatch>;
  gauge: Batch<GaugeBatch>;
} = {
  count: {},
  gauge: {},
};

export function makeThrottledMetricRequest(type: 'count' | 'gauge') {
  const pluralType = type + 's';
  return throttle(() => {
    const batch = batches[type];
    Object.keys(batch).forEach((serverName) => {
      Object.keys(batch[serverName]).forEach((serviceName) => {
        const serviceBatch = batch[serverName][serviceName];
        const payload: Record<string, unknown> = {
          api: serviceBatch.api,
          apiKey: serviceBatch.apiKey,
          headers: serviceBatch.headers,
          serverName,
          serviceName,
          // counts: serviceBatch.counts, OR
          // gauges: serviceBatch.gauges
          [pluralType]: serviceBatch[pluralType as keyof typeof serviceBatch],
        };
        // browserTime is used in browser requests only, to compensate for inaccurate browser clocks
        if (typeof window !== 'undefined') {
          payload.browserTime = Date.now();
        }
        post(type + 'Publisher/v2/add', payload);
      });
    });
    batches[type] = {};
  });
}

export const throttledSendCounts = makeThrottledMetricRequest('count');
export const throttledSendGauges = makeThrottledMetricRequest('gauge');

export function getBatch<BatchType>(metrics: MetricCounts | MetricGauges, type: 'count' | 'gauge'): BatchType {
  const { serverName = '', serviceName = '', api, apiKey, headers = {} } = metrics;
  const batch = batches[type];
  batch[serverName] = batch[serverName] || {};
  batch[serverName][serviceName] = batch[serverName][serviceName] || {
    api,
    apiKey,
    headers,
    [type + 's']: {},
  };
  const serviceBatch: unknown = batch[serverName][serviceName];
  return serviceBatch as BatchType;
}

export function mergeCounts(batchedCounts: MetricCounts['counts'], newCounts: MetricCounts['counts']) {
  Object.keys(newCounts).forEach((ms) => {
    batchedCounts[ms] = batchedCounts[ms] || {};
    Object.keys(newCounts[ms]).forEach((name) => {
      batchedCounts[ms][name] = batchedCounts[ms][name] || 0;
      batchedCounts[ms][name] += newCounts[ms][name];
    });
  });
}

export function mergeGauges(batchedGauges: MetricGauges['gauges'], newGauges: MetricGauges['gauges']) {
  Object.keys(newGauges).forEach((metricName) => {
    batchedGauges[metricName] = batchedGauges[metricName] || [];
    newGauges[metricName].forEach(([timestamp, value]) => {
      const existingGauge = batchedGauges[metricName].find((point) => point[0] === timestamp);
      if (existingGauge) {
        existingGauge[1] += value;
      } else {
        batchedGauges[metricName].push([timestamp, value]);
      }
    });
  });
}

export async function batchCounts(metrics: MetricCounts) {
  const batch = getBatch<CountBatch>(metrics, 'count');
  if (!batch.headers) {
    batch.headers = {};
  }
  if (metrics.headers) {
    Object.assign(batch.headers, metrics.headers);
  }
  mergeCounts(batch.counts, metrics.counts);
  throttledSendCounts();
}

export async function batchGauges(metrics: MetricGauges) {
  const batch = getBatch<GaugeBatch>(metrics, 'gauge');
  if (!batch.headers) {
    batch.headers = {};
  }
  if (metrics.headers) {
    Object.assign(batch.headers, metrics.headers);
  }
  mergeGauges(batch.gauges, metrics.gauges);
  throttledSendGauges();
}
