import { MonitorPlugin } from '@zg-rentals/monitor-base';
import type { GaugeOptions, IncrementOptions } from '@zg-rentals/monitor-base';
import type { Logger } from '@zg-rentals/logger-base';
import { Pontoon } from '@zg-rentals/pontoon';
import { NodeUtil } from '@zg-rentals/particles-js-utils';
import type { WebappInstance } from '@zg-rentals/pontoon';

export interface PontoonHeartbeatPluginArgs {
  api: string;
  apiKey: string;
  logger?: Logger;
  webappName: string;
  startupDate: number | Date | string;
  buildDate: number | Date | string;
  buildId?: number;
  commitId?: string;
  gitBranch?: string;
  propertiesFilePath?: string;
  processManagerId?: string;
}

const HEARTBEAT_INTERVAL_MS = 5_000;
const DEFAULT_PROPERTIES_FILE_PATH = '/hotpads/config/server.properties';
const REQUIRED_ARGS = ['api', 'apiKey', 'webappName', 'buildId', 'commitId', 'gitBranch', 'startupDate', 'buildDate'];

export class PontoonHeartbeatPlugin extends MonitorPlugin {
  pontoon: Pontoon;
  appData: WebappInstance;
  logger?: Logger;
  heartbeat?: NodeJS.Timeout;

  constructor({
    api,
    apiKey,
    webappName,
    startupDate,
    buildDate,
    buildId = 0,
    commitId = '',
    gitBranch = '',
    logger,
    propertiesFilePath = DEFAULT_PROPERTIES_FILE_PATH,
    processManagerId = process.env.pm_id,
  }: PontoonHeartbeatPluginArgs) {
    super({
      logger,
      pluginName: 'NodePontoonPlugin',
    });

    const { serverProperties, envProperties } = new NodeUtil.EnvironmentUtil(propertiesFilePath, {
      appName: webappName,
    });
    const serverName = serverProperties.name || '';
    const containerIdMatch = envProperties.hostname.match(/-\d+$/);
    const containerId = containerIdMatch ? containerIdMatch[0] : '';
    const processManagerIdSuffix = processManagerId ? `-${processManagerId}` : '';

    this.appData = {
      api,
      apiKey,
      webappName,
      serverName: `${serverName}${containerId}${processManagerIdSuffix}`,
      serverType: serverProperties.type,
      serverPublicIp: serverProperties.publicIp,
      serverPrivateIp: serverProperties.privateIp,
      buildId: buildId.toString(),
      commitId,
      gitBranch,
      startupDate: this.formatDate(startupDate),
      buildDate: this.formatDate(buildDate),
    };

    const missingArgs = REQUIRED_ARGS.filter((arg) => this.appData[arg as keyof WebappInstance] === undefined);
    if (missingArgs.length) {
      const errorMessage = `${this.pluginName} plugin missing required arg(s): ${missingArgs.join(', ')}`;
      if (this.logger) {
        this.logger.error(errorMessage);
      }
      throw new Error(errorMessage);
    }

    this.pontoon = new Pontoon({ api, apiKey });
  }

  initHeartbeat() {
    this.heartbeat = setInterval(() => {
      this.pontoon.addHeartbeat({
        ...this.appData,
        refreshedLast: this.formatDate(Date.now()),
      });
    }, HEARTBEAT_INTERVAL_MS);
  }

  onInitialize(increment: (i: IncrementOptions) => void, gauge: (g: GaugeOptions) => void, logger?: Logger) {
    this.logger = this.logger || logger;
    this.initHeartbeat();
  }

  async onShutdown() {
    if (this.heartbeat) {
      clearInterval(this.heartbeat);
      delete this.heartbeat;
    }
    await this.pontoon.removeHeartbeat({
      webappName: this.appData.webappName,
      serverName: this.appData.serverName,
    });
  }

  formatDate(date: string | Date | number = new Date()): string {
    if (!(date instanceof Date)) {
      date = new Date(date);
    }
    return date.toISOString();
  }
}
