import type { RequestHandler } from 'express';
import type { MonitorInterface } from '@zg-rentals/monitor-base';
import { BaseMonitor, MonitorPlugin, MonitorReporter } from '@zg-rentals/monitor-base';

export abstract class NodeMonitorPlugin extends MonitorPlugin {
  abstract onShutdown(): void;
}

export abstract class NodeMonitorReporter extends MonitorReporter {
  abstract onRequest(...args: Parameters<RequestHandler>): void;
}

export class NodeMonitor extends BaseMonitor<NodeMonitorPlugin, NodeMonitorReporter> {
  constructor(args: MonitorInterface<NodeMonitorPlugin, NodeMonitorReporter> = {}) {
    super(args);

    this._initializeProcessListeners();
    this._initializeOnEnd();
  }

  _initializeProcessListeners = (): void => {
    process.on('uncaughtExceptionMonitor', (err: Error, origin: 'uncaughtException' | 'unhandledRejection') => {
      this.logger &&
        this.logger.error(
          {
            stack: err.stack,
            name: err.name,
          },
          `[${origin}] node server about to crash: ${err.message}`,
        );

      this.error({ error: err });
    });
  };

  _initializeOnEnd = (): void => {
    process.on('SIGINT', () => {
      this.logger && this.logger.warn(`SIGINT detected, running plugins onEnd`);

      this.plugins.map((plugin) => {
        plugin.onShutdown();
      });

      this.logger && this.logger.warn(`notify shutdown listener finished`);

      // todo: hack: fix for pino needing to finish writing
      // before process terminates
      // should work to implement pino.final and custom pino.destination in ServerLogger
      // https://github.com/pinojs/pino/blob/master/docs/api.md#pino-final
      setTimeout(() => process.exit(), 1000);
    });
  };

  async onRequest(...args: Parameters<RequestHandler>) {
    await Promise.all(
      this.reporters.map((reporter) => {
        return reporter.onRequest(...args);
      }),
    );
    args[2]();
  }
}
