import type { IncomingMessage, ServerResponse } from 'http';
import type { Level } from 'pino';
import pinoHttp from 'pino-http';

export interface RequestSerializerInput extends IncomingMessage {
  raw: {
    get: (s: string) => string;
  };
}

interface GetHttpLoggerInterface extends pinoHttp.Options {
  level?: Level;
}

export const defaultSerializers = {
  req: (req: RequestSerializerInput) => ({
    id: req.id as string,
    ip: req.raw.get('X-Client-Ip'),
    userAgent: req.raw.get('User-Agent'),
    referer: req.raw.get('Referer'),
    method: req.method,
    url: req.url,
  }),
  res: (res: ServerResponse) => ({
    statusCode: res.statusCode,
  }),
  err: (err: Error) => ({
    name: err.name,
    stack: err.stack?.replace(/\s+/g, ' '),
    message: err.message,
  }),
};

export function getHttpLogger(args: GetHttpLoggerInterface) {
  if (!args.logger) {
    throw new Error('must pass a Logger to getHttpLogger');
  }

  return pinoHttp({
    genReqId: (req: IncomingMessage) => req.id || '',
    customLogLevel: (res, err) => (err ? 'error' : args.level || 'info'),
    ...args,
    serializers: {
      ...defaultSerializers,
      ...(args.serializers || {}),
    },
  });
}
