import type http from 'http';
import type { Request } from 'express';

import cookie from 'cookie';

const BRANDS = ['hotpads', 'trulia', 'zillow'];

export function findBrand(search?: string) {
  if (!search) return '';

  const foundBrand = BRANDS.find((brand) => {
    return search.includes(brand);
  });

  if (foundBrand) {
    return foundBrand;
  } else {
    // staging server specific to brands
    // useful for production builds (do not respect cookie) deployed on non-production servers
    if (search.includes('testpads')) {
      return 'hotpads';
    }
  }

  return '';
}

export function getBrand(req: http.IncomingMessage | Request, isDev = true) {
  let brand: string | null = null;

  if (isDev) {
    if ('cookies' in req) {
      brand = findBrand(req.cookies.dev_brand);
    }

    if (brand) {
      return brand;
    }

    if (req.headers.cookie) {
      const headerCookies = cookie.parse(req.headers.cookie);

      brand = findBrand(headerCookies.dev_brand);

      if (brand) {
        return brand;
      }
    }
  }

  brand = findBrand((req.headers['X-Forwarded-Host'] as string) || req.headers.host);

  if (brand) {
    return brand;
  }

  // array.find returns undefined if no match
  // cannot return undefined, will break request headers
  if (!brand) {
    return null;
  }
}
