import type { Response } from 'node-fetch';
import type { Logger } from '.';
import { getLoggerChildren } from '@zg-rentals/logger-base';
import fetch from 'node-fetch';

type LogLevel = 'warn' | 'error' | 'trace' | 'info' | 'debug' | 'fatal';

type GetNodeLoggersResponse = Array<{
  name: string;
  level: LogLevel;
}>;

export async function logSync({
  logger,
  lookoutApi = 'https://lookout.hotpads.com',
  apiKey,
  app,
}: {
  logger: Logger;
  lookoutApi?: string;
  apiKey: string;
  app: string;
}) {
  async function runSync() {
    try {
      await registerChildLoggers({ logger, lookoutApi, apiKey });
      const levelsFromApi = await getChildLoggerLevels({ apiKey, lookoutApi, app });

      await setChildLogLevels({
        logger,
        levelsFromApi,
      });
    } catch (e) {
      logger && logger.error(e);
      return;
    }
  }

  runSync();

  setTimeout(async () => {
    await runSync();
  }, 5000);
}

export class ResponseError extends Error {
  errors: Array<Error>;
  constructor(errors: Array<Error>) {
    super();
    this.errors = errors;
  }

  toString() {
    return this.errors.map(({ message }) => message).join(', ');
  }
}

export async function parseLookoutResponse<T>(res: Response): Promise<T | string> {
  const contentType = res.headers.get('content-type');

  if (res.status !== 200) {
    throw new ResponseError([new Error(`error querying ${res.url}: ${res.statusText} - ${res.status}`)]);
  } else if (contentType && contentType.includes('application/json')) {
    return await res.json();
  } else {
    return await res.text();
  }
}

export async function registerChildLoggers({
  logger,
  lookoutApi = 'https://lookout.hotpads.com',
  apiKey,
}: {
  logger: Logger;
  lookoutApi?: string;
  apiKey: string;
}) {
  const loggers = Object.keys(getLoggerChildren(logger));

  const url = new URL('lookout/api/node/logger/register', lookoutApi);
  url.search = new URLSearchParams({ apiKey }).toString();

  const res = await fetch(url, {
    method: 'post',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({
      env: process.env.ZG_ENV,
      loggers,
    }),
  });

  return (await parseLookoutResponse<''>(res)) as string;
}

export async function getChildLoggerLevels({
  apiKey,
  app,
  lookoutApi = 'https://lookout.hotpads.com',
}: {
  app: string;
  lookoutApi?: string;
  apiKey: string;
}) {
  const url = new URL('lookout/api/node/logger/get', lookoutApi);
  url.search = new URLSearchParams({
    apiKey,
    app,
    env: process.env.ZG_ENV || '',
  }).toString();

  const res = await fetch(url, {
    method: 'get',
    headers: { 'Content-Type': 'application/json' },
  });

  return (await parseLookoutResponse<GetNodeLoggersResponse>(res)) as GetNodeLoggersResponse;
}

export async function setChildLogLevels({
  logger,
  levelsFromApi,
}: {
  logger: Logger;
  levelsFromApi: GetNodeLoggersResponse;
}) {
  const customLevelLoggers = levelsFromApi.reduce<Record<string, string>>((acc, child) => {
    acc[child.name] = child.level;

    return acc;
  }, {});

  const children = getLoggerChildren(logger);

  Object.keys(children).forEach((name) => {
    if (customLevelLoggers[name]) {
      children[name].level = customLevelLoggers[name];
    }
  });
}
