import axios from 'axios';
import type {
  HeartbeatDescriptor,
  MetricAlert,
  MetricCounts,
  MetricGauges,
  MetricQueryRequest,
  MetricWindow,
  PontoonOptions,
  ServiceConfigDescriptor,
  ServiceConfiguration,
  StatUrlOptions,
  WebappBatchDescriptor,
  WebappDescriptor,
  WebappInstance,
} from './request-types';
import { batchCounts, batchGauges } from './batched-metrics';
import type {
  GenericResponse,
  MetricQueryDataResponse,
  MetricWindowResponse,
  ServiceConfigResponse,
  WebappInstanceResponse,
} from './response-types';
import type { Stat } from '@zg-rentals/monitor-base';

export async function post<K>(apiPath: string, body: K & PontoonOptions) {
  const { api, apiKey, headers, ...data } = body;
  const response = await axios.post(`${api}/pontoon/api/${apiPath}`, data, {
    params: { apiKey },
    headers,
  });
  return response.data;
}

export async function get<K>(apiPath: string, opts: K & PontoonOptions) {
  const { api, headers, ...params } = opts;
  const response = await axios.get(`${api}/pontoon/api/${apiPath}`, {
    params,
    headers,
  });
  return response.data;
}

export function sendCounts(metrics: MetricCounts): Promise<void> {
  return batchCounts(metrics);
}

export function sendGauges(metrics: MetricGauges): Promise<void> {
  return batchGauges(metrics);
}

export function addServiceConfig(serviceConfig: ServiceConfiguration): Promise<GenericResponse> {
  return post('externalServiceConfiguration/add', serviceConfig);
}

export function getServiceConfig(descriptor: ServiceConfigDescriptor): Promise<ServiceConfigResponse> {
  return get('externalServiceConfiguration/get', descriptor);
}

export async function getAllServiceConfigs(options: PontoonOptions): Promise<Array<ServiceConfigResponse>> {
  const results = await get('externalServiceConfiguration/getAll', options);
  return results.batch;
}

export function areGaugesDelayed(options: PontoonOptions): Promise<boolean> {
  return get('gaugeAggregation/isDelayed', options);
}

export function registerAlert(alert: MetricAlert): Promise<void> {
  return post('metric/alert/register', alert);
}

export function getWindow(metricWindow: MetricWindow): Promise<MetricWindowResponse> {
  return post('metric/getWindow', metricWindow);
}

export function query(metricQuery: MetricQueryRequest): Promise<MetricQueryDataResponse> {
  return post('metric/query', metricQuery);
}

export function addHeartbeat(webApp: WebappInstance): Promise<GenericResponse> {
  webApp.servletContainerVersion = webApp.servletContainerVersion || '';
  webApp.servletContextPath = webApp.servletContextPath || '';
  return post('webappInstancePublisher/add', webApp);
}

export function removeHeartbeat(descriptor: HeartbeatDescriptor): Promise<GenericResponse> {
  return get('webappInstancePublisher/delete', descriptor);
}

export async function getAllWebapps(descriptor: WebappBatchDescriptor): Promise<Array<WebappInstanceResponse>> {
  const results = await get('webappInstanceReader/get', descriptor);
  return results.dtos;
}

export function getWebapp(webApp: WebappDescriptor): Promise<WebappInstanceResponse> {
  return get('webappInstanceReader/getInstance', webApp);
}

export function addStatUrl({ stat, serviceName, api }: StatUrlOptions) {
  const statUrlParams = {
    periodMs: 300000,
    frequency: 'period',
    regexSplitLimit: 50,
    entries: [
      {
        accountName: serviceName,
        metricInput: stat.name,
        serviceName,
        serverName: 'All',
        splitServer: false,
      },
    ],
  };
  const queryString = encodeURIComponent(JSON.stringify(statUrlParams));
  stat.url = `${api}/pontoon/metrics/viewMetrics/view?q=${queryString}`;
  return stat;
}

// exported functions may be called directly or called as methods of a Pontoon instance
export class Pontoon {
  options: PontoonOptions;

  constructor(options: PontoonOptions) {
    if (typeof window === 'undefined' && !(options.api && options.apiKey)) {
      throw new Error("Can't query Pontoon without the api and apiKey options.");
    }
    this.options = options;
  }

  // Wraps a function so that the `api` and `apiKey` options will be added
  // to whatever object is passed in. Without typing it would look like this:
  // addOpts: request => options => request({ ...this.options, ...options })
  private addOpts<T, S>(request: (options: T) => S) {
    return (options: T) =>
      request({
        ...this.options,
        ...options,
      });
  }

  sendCounts = this.addOpts<MetricCounts, Promise<void>>(sendCounts);
  addServiceConfig = this.addOpts<ServiceConfiguration, Promise<GenericResponse>>(addServiceConfig);
  getServiceConfig = this.addOpts<ServiceConfigDescriptor, Promise<ServiceConfigResponse>>(getServiceConfig);
  getAllServiceConfigs = this.addOpts<PontoonOptions, Promise<Array<ServiceConfigResponse>>>(getAllServiceConfigs);
  areGaugesDelayed = this.addOpts<PontoonOptions, Promise<boolean>>(areGaugesDelayed);
  sendGauges = this.addOpts<MetricGauges, Promise<void>>(sendGauges);
  registerAlert = this.addOpts<MetricAlert, Promise<void>>(registerAlert);
  getWindow = this.addOpts<MetricWindow, Promise<MetricWindowResponse>>(getWindow);
  query = this.addOpts<MetricQueryRequest, Promise<MetricQueryDataResponse>>(query);
  addHeartbeat = this.addOpts<WebappInstance, Promise<GenericResponse>>(addHeartbeat);
  removeHeartbeat = this.addOpts<HeartbeatDescriptor, Promise<GenericResponse>>(removeHeartbeat);
  getAllWebapps = this.addOpts<WebappBatchDescriptor, Promise<Array<WebappInstanceResponse>>>(getAllWebapps);
  getWebapp = this.addOpts<WebappDescriptor, Promise<WebappInstanceResponse>>(getWebapp);
  addStatUrl = this.addOpts<StatUrlOptions, Stat>(addStatUrl);
}
