/* eslint-disable @typescript-eslint/ban-types */

export type Tags = Record<string, unknown>;

export interface Trace {
  traceId: string;
  sessionId: string;
  reqId?: string;
  doSave: boolean;
  tags: Tags;
}

export interface Span {
  spanId: string;
  traceId: string;
  finish: (error?: Error) => void;
  getTags: () => Tags;
  setTags: (tags: Tags) => void;
}

// ASYNC_FN_MATCHER matches any stringified function that accepts one or more arguments/callbacks
// YES: (arg) => { ... }
// YES: arg => { ... }
// YES: function hello ( goodbye ) { ... }
// NO: ( ) => { ... }
// NO: function test() { ... }
// NO: function( ) { ... }
export const ASYNC_FN_MATCHER = /^(\(?\s*\w+\s*\)?\s*=>|function(\s+\w*\s*)?\(\s*\w)/;

export abstract class TracerInterface {
  abstract trace<T>(fn: (...args: Array<unknown>) => T, name: string, tags?: Tags): T;
  abstract wrap<T extends Function>(fn: T, name: string, tags?: Tags): T;
  abstract setSpanTag(key: string, value: unknown): void;
  abstract getSpanTag(key: string): void;
  abstract getCurrentTrace(): Trace;
  abstract getCurrentSpan(): Span | undefined;
  abstract getCurrentTags(): Tags;
  abstract setSaveTrace(save: boolean): void;
}

export default class BaseTracer implements TracerInterface {
  static isAsyncFunction(fn: Function) {
    return !!fn.toString().match(ASYNC_FN_MATCHER);
  }

  trace<T>(fn: (...args: Array<unknown>) => T, name: string, tags?: Tags) {
    throw new Error(`Not Implemented: Tracer.trace(${fn}, ${name}, ${tags})`);
    return fn();
  }

  wrap<T extends Function>(fn: T, name = fn.name, tags?: Tags) {
    return ((...args: Array<unknown>) => {
      return this.trace(() => fn.apply(this, args), name, tags);
    }) as unknown as T;
  }

  setSpanTag(key: string, value: unknown) {
    const span = this.getCurrentSpan();
    if (span) {
      span.setTags({ [key]: value });
    }
  }

  getSpanTag(key: string) {
    const span = this.getCurrentSpan();
    if (span) {
      return span.getTags()[key];
    }
  }

  getCurrentTrace() {
    throw new Error(`Not Implemented: Tracer.getCurrentTrace()`);
    return {
      traceId: 'traceId',
      sessionId: 'sessionId',
      doSave: false,
      tags: {} as Tags,
    };
  }

  getCurrentSpan(): Span | undefined {
    throw new Error(`Not Implemented: Tracer.getCurrentSpan()`);
    return undefined;
  }

  getCurrentTags() {
    const trace = this.getCurrentTrace();
    const span = this.getCurrentSpan();
    return {
      ...trace.tags,
      ...span?.getTags(),
    };
  }

  setSaveTrace(doSave: boolean) {
    const trace = this.getCurrentTrace();
    trace.doSave = doSave;
  }
}
