import type { Logger, LoggerOptions } from '@zg-rentals/logger-base';
import { enforceSampleRate, trackChildLoggers } from '@zg-rentals/logger-base';
import pino from 'pino';
import pinoPretty from 'pino-pretty';
import { Writable } from 'stream';

// @ts-ignore pino-pretty typing is incorrect
const levelColorize = pinoPretty.colorizerFactory(true);

let activeLoggers: Record<string, Logger> = {};

const levelLabels = {
  '10': 'trace',
  '20': 'debug',
  '30': 'info',
  '40': 'warn',
  '50': 'error',
  '60': 'fatal',
};

const transports = {
  silent: () =>
    new Writable({
      write: (chunk, encoding, callback) => callback(),
    }),
  pretty: () =>
    pinoPretty({
      destination: 1,
      colorize: true,
      ignore: 'pid,hostname',
      customPrettifiers: {
        // @ts-ignore pino-pretty typing is incorrect
        level: (level) => (level === 30 ? '' : levelColorize(level)),
      },
    }),
  plain: () =>
    pinoPretty({
      destination: 1,
      colorize: false,
      ignore: 'pid,hostname',
      customPrettifiers: {
        // @ts-ignore pino-pretty typing is incorrect
        level: (level) => (level === 30 ? '' : `${levelLabels[level]}:`),
      },
    }),
  file: (logPath?: string) =>
    pino.transport({
      targets: [
        // @ts-ignore type definition is wrong
        {
          target: 'pino/file',
          options: {
            destination: 1,
          },
        },
        // @ts-ignore type definition is wrong
        {
          target: 'pino/file',
          options: {
            destination: logPath,
            mkdir: true,
          },
        },
      ],
    }),
};

export function resetLoggers() {
  activeLoggers = {};
}

export type NodeLoggerOptions = LoggerOptions & {
  colorize?: boolean;
};

export default function getNodeLogger({
  logPath,
  silent,
  colorize = true,
  ...options
}: NodeLoggerOptions = {}): Logger {
  let loggerType: keyof typeof transports = 'pretty';
  if (silent || options.level === 'silent') {
    loggerType = 'silent';
  } else if (logPath) {
    loggerType = 'file';
  } else if (process.env.CI === 'true' || !colorize) {
    loggerType = 'plain';
  }

  let logger = activeLoggers[logPath || loggerType];

  if (logger) {
    if (options.level && options.level !== logger.level) {
      logger.level = options.level;
    }
    return logger;
  }

  logger = pino(
    {
      level: 'info',
      timestamp: !!logPath,
      ...options,
      hooks: enforceSampleRate(options),
    },
    transports[loggerType](logPath),
  ) as unknown as Logger;

  trackChildLoggers(logger);

  activeLoggers[logPath || loggerType] = logger;

  return logger;
}
