export interface BatchInterface<T> {
  flushIntervalMs?: number;
  flushItemsThreshold?: number;
  flushUniqueItemsThreshold?: number;
  onFlush: (items: Array<T>) => Promise<unknown>;
  debug?: boolean;
}

const defaults = {
  flushIntervalMs: 5_000,
  flushItemsThreshold: 3_000,
  flushUniqueItemsThreshold: 150,
};

const LOG_PREFIX = 'particles-js-utils#Batch: ';

export class Batch<T> {
  public flushIntervalMs: number;
  public flushItemsThreshold: number;
  public flushUniqueItemsThreshold: number;
  public flushTimeout: NodeJS.Timeout | undefined;
  public flushing: boolean;
  public onFlush: (items: Array<T>) => Promise<unknown>;
  public bufferUniqueIds: Set<string>;
  public buffer: Array<T>;

  // don't use our particles logger, since this class is used by the logger at instantiation
  public logger: Console;
  private debug: boolean;

  constructor(options: BatchInterface<T>) {
    this.flushIntervalMs = options.flushIntervalMs || defaults.flushIntervalMs;
    this.flushItemsThreshold = options.flushItemsThreshold || defaults.flushItemsThreshold;
    this.flushUniqueItemsThreshold = options.flushUniqueItemsThreshold || defaults.flushUniqueItemsThreshold;
    this.flushing = false;
    this.onFlush = options.onFlush;
    this.logger = console;
    this.debug = options.debug || false;
    this.bufferUniqueIds = new Set();
    this.buffer = [];

    this.reset();
    this.startFlushTimeout();
  }

  log = (...args: Array<unknown>) => {
    if (!this.debug) return;

    this.logger.debug(LOG_PREFIX, ...args);
  };

  hasHitUniqueThreshold = (): boolean => {
    const { bufferUniqueIds, flushUniqueItemsThreshold, log } = this;

    const brokeThreshold = bufferUniqueIds.size >= flushUniqueItemsThreshold;

    if (!brokeThreshold) {
      return false;
    }

    log('batch unique threshold reached, trigger flush', {
      uniqueIdSize: bufferUniqueIds.size,
      threshold: flushUniqueItemsThreshold,
    });

    return true;
  };

  hasHitSizeThreshold = (): boolean => {
    const { buffer, flushItemsThreshold, log } = this;

    const brokeThreshold = buffer.length >= flushItemsThreshold;

    if (!brokeThreshold) {
      return false;
    }

    log('batch buffer size threshold reached, trigger flush', {
      bufferSize: buffer.length,
      threshold: flushItemsThreshold,
    });

    return true;
  };

  public add = (id: string, metric: T): Promise<unknown> => {
    this.bufferUniqueIds.add(id);
    this.buffer.push(metric);

    if (!this.flushing && (this.hasHitSizeThreshold() || this.hasHitUniqueThreshold())) {
      return this.flush();
    }

    return Promise.resolve();
  };

  finishFlush = () => {
    this.flushing = false;

    if (!this.flushTimeout) {
      this.startFlushTimeout();
    }
  };

  public flush = (): Promise<unknown> => {
    const { buffer: items, log, reset, onFlush } = this;

    this.flushing = true;

    log('triggering flush');

    reset();

    if (!items.length) {
      log('nothing to flush');

      this.finishFlush();
      return Promise.resolve();
    }

    return onFlush(items).finally(() => {
      this.finishFlush();
    });
  };

  private reset = () => {
    this.bufferUniqueIds = new Set();
    this.buffer = [];

    if (this.flushTimeout) {
      clearTimeout(this.flushTimeout);
      this.flushTimeout = undefined;
    }
  };

  private startFlushTimeout = () => {
    this.log('starting flush timeout', this.flushIntervalMs);

    this.flushTimeout = setTimeout(() => this.flush(), this.flushIntervalMs);
  };
}
