import type { Logger } from '@zg-rentals/logger-base';

import type { MonitorPlugin } from './plugin';
import type { MonitorReporter } from './reporter';
import type { StatController } from './statController';

export interface MonitorInterface<PluginType, ReporterType> {
  logger?: Logger;
  plugins?: Array<PluginType>;
  reporters?: Array<ReporterType>;
}

export interface MetricAlert {
  periodMs: number;
  minThreshold?: number;
  maxThreshold?: number;
  alertFrequencyMinutes: number;
  customEmails?: Array<string>;
  metricName?: string;
  metricType?: 'GAUGE' | 'COUNT';
}

export interface MetricOptions {
  tags?: Record<string, unknown>;
  alert?: MetricAlert;
  [key: string]: unknown;
}

export interface IncrementOptions {
  name: string;
  amount?: number;
  options?: MetricOptions;
}

export interface GaugeOptions {
  name: string;
  amount?: number;
  options?: MetricOptions;
}
export interface Monitor {
  increment: (opts: IncrementOptions) => Promise<void>;
  gauge: (opts: GaugeOptions) => Promise<void>;
  error: ({ error }: { error: Error }) => Promise<void>;
}

export class BaseMonitor<PluginType extends MonitorPlugin, ReporterType extends MonitorReporter> implements Monitor {
  public plugins: Array<PluginType>;
  public reporters: Array<ReporterType>;
  public stats: Record<string, StatController>;

  protected logger?: Logger;

  constructor({ logger, plugins = [], reporters = [] }: MonitorInterface<PluginType, ReporterType> = {}) {
    this.logger = logger;
    this.plugins = plugins;
    this.reporters = reporters;
    this.stats = {};

    this._initializeReporters = this._initializeReporters.bind(this);
    this._trackMonitorStart = this._trackMonitorStart.bind(this);
    this._initializePlugins = this._initializePlugins.bind(this);
    this.increment = this.increment.bind(this);
    this.error = this.error.bind(this);
    this.gauge = this.gauge.bind(this);

    this._initializeReporters();
    this._trackMonitorStart();
    this._initializePlugins();
  }

  private _initializeReporters() {
    this.reporters.forEach((reporter) => {
      reporter.onInitialize(this.logger);
      if (reporter.stats) {
        this.stats[reporter.reporterName] = reporter.stats;
      }
    });
  }

  private _initializePlugins() {
    this.plugins.forEach((plugin) => {
      plugin.onInitialize(this.increment.bind(this), this.gauge.bind(this), this.logger);
      if (plugin.stats) {
        this.stats[plugin.pluginName] = plugin.stats;
      }
    });
  }

  private async _trackMonitorStart() {
    await this.increment({ name: 'Monitor started' });
  }

  async increment(...counts: Array<IncrementOptions>) {
    const countPromises: Array<Promise<void>> = [];
    counts.forEach(({ name, amount = 1, options }) => {
      this.reporters.forEach((reporter) => {
        countPromises.push(
          reporter.onIncrement({
            name,
            amount,
            options,
          }),
        );
      });
    });
    await Promise.all(countPromises);
  }

  async gauge(...gauges: Array<GaugeOptions>) {
    const gaugePromises: Array<Promise<void>> = [];
    gauges.forEach(({ name, amount = 1, options }) => {
      this.reporters.forEach((reporter) => {
        gaugePromises.push(
          reporter.onGauge({
            name,
            amount,
            options,
          }),
        );
      });
    });
    await Promise.all(gaugePromises);
  }

  async error({ error }: { error: Error }) {
    await Promise.all(
      this.reporters.map((reporter) => {
        return reporter.onError({ error });
      }),
    );
  }
}
