import type http from 'http';

/* eslint-disable @typescript-eslint/no-var-requires */
const ipRangeCheck = require('ip-range-check');

// TODO: inherently insecure to ip spoofing
// x-client-ip is only set on node -> node proxy
// need to protect against x-client-ip spoof on client requests

// req.get is a case-insensitive match
// http://expressjs.com/en/api.html#req.get
export function getIpsFromReq(req: http.IncomingMessage) {
  const zgIpHeader = req.headers['x-client-ip'] || req.headers['X-Client-Ip'];
  const xForwadedFor = req.headers['x-forwarded-for'];
  const socketRemoteAddres = (req.socket || {}).remoteAddress;

  return {
    zgIpHeader,
    xForwadedFor,
    socketRemoteAddres,
    defaultIpAddress: '0.0.0.0',
  };
}

export function selectValidIp(req: http.IncomingMessage): string {
  const { zgIpHeader, xForwadedFor, socketRemoteAddres, defaultIpAddress } = getIpsFromReq(req);
  const ipByPrecedence = zgIpHeader || xForwadedFor || socketRemoteAddres || defaultIpAddress;

  // deal with comma delimited ips
  if (!Array.isArray(ipByPrecedence) && ipByPrecedence.indexOf(',') > -1) {
    const ipArr = ipByPrecedence.replace(/ /g, '').split(',');
    const filteredIps = ipArr.filter((ip) => !ipRangeCheck(ip, '10.0.0.0/8') && !ipRangeCheck(ip, '172.16.1.0/12') && !ipRangeCheck(ip, '100.64.0.0/10'));

    if (filteredIps.length) {
      return filteredIps[0];
    }

    return defaultIpAddress;
  }

  return Array.isArray(ipByPrecedence) ? ipByPrecedence[0] : ipByPrecedence;
}
