export interface Stat {
  name: string;
  count: number;
  url?: string;
}

export class StatController {
  onRecord: ({ name, count }: Stat) => Stat;

  stats: Record<string, Stat>;
  limit: number;

  constructor({
    onRecord,
    limit = 50,
  }: {
    onRecord?: ({ name, count }: Stat) => Stat;
    limit?: number;
  } = {}) {
    this.onRecord = onRecord || (({ name, count }) => ({ name, count }));
    this.stats = {};
    this.limit = limit;
  }

  record = ({ name, count = 1 }: { name: string; count?: number }): Stat => {
    const existingStat = this.stats[name];

    if (!existingStat) {
      const keys = Object.keys(this.stats);
      if (keys.length >= this.limit) {
        delete this.stats[keys[0]];
      }

      const stat = this.onRecord({ name, count });

      this.stats[name] = stat;

      return stat;
    }

    existingStat.count = existingStat.count + count;

    return existingStat;
  };
}
