import os from 'os';
import fs from 'fs';
import v8 from 'v8';
import { constants, PerformanceObserver } from 'perf_hooks';
import eventLoopStats from 'event-loop-stats';
import type { NodeVitalsPlugin } from '.';
import type { PerformanceEntry } from 'perf_hooks';

const gcFlagMap = {
  [constants.NODE_PERFORMANCE_GC_MAJOR]: 'MarkSweepCompact',
  [constants.NODE_PERFORMANCE_GC_MINOR]: 'Scavange',
  [constants.NODE_PERFORMANCE_GC_INCREMENTAL]: 'IncrementalMarking',
  [constants.NODE_PERFORMANCE_GC_WEAKCB]: 'ProcessWeakCallbacks',
};

let observer: PerformanceObserver;

export const Bytes = {
  toKb: (bytes: number) => Math.round(bytes / 1024),
  toMb: (bytes: number) => Math.round(bytes / 1024 / 1024),
};

export default {
  start({ increment }: NodeVitalsPlugin) {
    if (increment) {
      increment({ name: 'App startup', amount: 1 });

      observer = new PerformanceObserver((list) => {
        list.getEntries().forEach((entry: PerformanceEntry) => {
          // @ts-ignore the type definition is missing this property
          const kindFlag: number = entry.kind;
          const typeName = `${kindFlag}-${gcFlagMap[kindFlag]}`;
          const pauseMS = Math.round(entry.duration);
          increment({ name: 'gc type ' + typeName, amount: 1 });
          increment({ name: 'gc pause ms', amount: pauseMS });
          increment({ name: 'gc type ' + typeName + ' pause ms', amount: pauseMS });
        });
      });

      observer.observe({
        entryTypes: ['gc'],
      });
    }
  },

  stop() {
    observer.disconnect();
  },

  record({ buildInfo, increment, gauge }: NodeVitalsPlugin) {
    const serverType = buildInfo.serverType;
    const loopStats = eventLoopStats.sense();

    if (increment) {
      increment({ name: 'App heartbeat ' + serverType, amount: 1 });
      increment({ name: 'App heartbeat type-build ' + serverType + ' ' + buildInfo.buildNumber, amount: 1 });
      increment({ name: 'App heartbeat type-commit ' + serverType + ' ' + buildInfo.gitCommit, amount: 1 });
      increment({ name: 'App heartbeat build ' + buildInfo.buildNumber, amount: 1 });
      increment({ name: 'App heartbeat commit ' + buildInfo.gitCommit, amount: 1 });
      increment({ name: 'event loops', amount: loopStats.num });
    }

    if (gauge) {
      const loadavg = os.loadavg();
      const loadavgScaler = 1_000; //pontoon rounds to integer, scale for additional precision
      gauge({ name: 'os load avg 1m', amount: loadavg[0] * loadavgScaler });
      gauge({ name: 'os load avg 5m', amount: loadavg[1] * loadavgScaler });
      gauge({ name: 'os load avg 15m', amount: loadavg[2] * loadavgScaler });

      const totalmem = os.totalmem();
      const freemem = os.freemem();
      const usedmem = totalmem - freemem;
      gauge({ name: 'memory total MB', amount: Bytes.toMb(totalmem) });
      gauge({ name: 'memory free MB', amount: Bytes.toMb(freemem) });
      gauge({ name: 'memory used MB', amount: Bytes.toMb(usedmem) });

      const memoryUsage = process.memoryUsage();
      gauge({ name: 'node memory mb rss', amount: Bytes.toMb(memoryUsage.rss) });
      gauge({ name: 'node memory mb heapTotal', amount: Bytes.toMb(memoryUsage.heapTotal) });
      gauge({ name: 'node memory mb heapUsed', amount: Bytes.toMb(memoryUsage.heapUsed) });
      gauge({ name: 'node memory mb external', amount: Bytes.toMb(memoryUsage.external) });

      v8.getHeapSpaceStatistics().forEach(
        ({ space_name, space_size, space_used_size, space_available_size, physical_space_size }) => {
          const prefix = 'v8 heap kb ' + space_name + ' ';
          gauge({ name: prefix + 'space_size', amount: Bytes.toKb(space_size) });
          gauge({ name: prefix + 'space_used_size', amount: Bytes.toKb(space_used_size) });
          gauge({ name: prefix + 'space_available_size', amount: Bytes.toKb(space_available_size) });
          gauge({ name: prefix + 'physical_space_size', amount: Bytes.toKb(physical_space_size) });
        },
      );

      gauge({ name: 'event loop ms max', amount: loopStats.max });
      gauge({ name: 'event loop ms min', amount: loopStats.min });
      gauge({ name: 'event loop ms avg', amount: loopStats.sum / loopStats.num });

      fs.readFile('/proc/sys/fs/file-nr', (err, out) => {
        if (!err) {
          const fileNrParts = out.toString().replace(/\n/g, '').split(' ');
          const fileHandles = parseInt(fileNrParts[0], 10);
          gauge({ name: 'file-nr allocated file handles', amount: fileHandles });
        }
      });
    }
  },
};
