import { MonitorPlugin } from '@zg-rentals/monitor-base';
import type { GaugeOptions, IncrementOptions } from '@zg-rentals/monitor-base';
import type { Logger } from '@zg-rentals/logger-base';
import { NodeUtil } from '@zg-rentals/particles-js-utils';
import nodeVitals from './nodeVitals';

export interface NodeVitalsPluginArgs {
  logger?: Logger;
  webappName: string;
  buildNumber?: number;
  gitCommit?: string;
  propertiesFilePath?: string;
}

export interface NodeVitalsBuildInfo {
  serverType: string;
  buildNumber: string;
  gitCommit: string;
}

const DEFAULT_PROPERTIES_FILE_PATH = '/hotpads/config/server.properties';
const INTERVAL_MS = 5_000;

export class NodeVitalsPlugin extends MonitorPlugin {
  buildInfo: NodeVitalsBuildInfo;
  logger?: Logger;
  interval?: NodeJS.Timeout;

  constructor({
    webappName,
    buildNumber = 0,
    gitCommit = '',
    logger,
    propertiesFilePath = DEFAULT_PROPERTIES_FILE_PATH,
  }: NodeVitalsPluginArgs) {
    super({
      logger,
      pluginName: 'NodePontoonPlugin',
    });

    const { serverProperties } = new NodeUtil.EnvironmentUtil(propertiesFilePath, { appName: webappName });

    this.buildInfo = {
      serverType: serverProperties.type,
      buildNumber: buildNumber.toString(),
      gitCommit,
    };

    const requiredArgs = ['buildNumber', 'gitCommit'];
    const missingArgs = requiredArgs.filter(
      (arg) => this.buildInfo[arg as keyof NodeVitalsPlugin['buildInfo']] === undefined,
    );
    if (missingArgs.length) {
      const errorMessage = `${this.pluginName} plugin missing required arg(s): ${missingArgs.join(', ')}`;
      if (this.logger) {
        this.logger.error(errorMessage);
      }
      throw new Error(errorMessage);
    }
  }

  initVitals() {
    nodeVitals.start(this);
    this.interval = setInterval(() => nodeVitals.record(this), INTERVAL_MS);
  }

  onInitialize(increment: (i: IncrementOptions) => void, gauge: (g: GaugeOptions) => void, logger?: Logger) {
    this.increment = increment;
    this.gauge = gauge;
    this.logger = this.logger || logger;
    this.initVitals();
  }

  async onShutdown() {
    if (this.interval) {
      clearInterval(this.interval);
      delete this.interval;
    }
    nodeVitals.stop();
  }
}
