import type { Span, Trace } from '@zg-rentals/trace-base';
import { BaseTracerPlugin } from './plugins';
import crypto from 'crypto';
import httpContext from 'express-http-context';
import http from 'http';
import https from 'https';
import { URL } from 'url';
import type { Application, NextFunction, Request, Response } from 'express';
import type { NodeTracerPlugin } from './plugins';
import nanoTime from 'nano-time';
import BaseTracer from '@zg-rentals/trace-base';

export const TRACE_COOKIE_NAME = 'rjs-trace';
export const TRACE_COOKIE_DURATION_SEC = 31536000;
export const SESSION_ID_LENGTH = 26;
export const TRACE_ID_LENGTH = 32;
export const SPAN_ID_LENGTH = 16;

export interface NodeTracerOptions {
  plugin?: NodeTracerPlugin;
  app?: Application;
  saveAllTraces?: boolean;
}

export type HttpCallback = (res: http.IncomingMessage) => void;

let requestsIntercepted: boolean = false;

const ERROR_KEYS = ['error', 'stack', 'err', 'errorStack', 'trace'];

export class NodeTracer extends BaseTracer {
  plugin: NodeTracerPlugin;
  app?: Application;
  saveAllTraces: boolean;

  constructor({ app, plugin, saveAllTraces = false }: NodeTracerOptions = {}) {
    super();

    if (typeof window !== 'undefined') {
      throw new Error('@zg-rentals/trace-node should not be included in client-side code.');
    }

    this.saveAllTraces = saveAllTraces;

    this.plugin = plugin || new BaseTracerPlugin();

    if (app) {
      this.traceApp(app);
    }

    if (!requestsIntercepted) {
      this.interceptRequests(http);
      this.interceptRequests(https);
    }

    requestsIntercepted = true;
  }

  interceptRequests(protocol: typeof http | typeof https) {
    const originalRequest = protocol.request;
    const wrappedRequest = (
      urlOrOptions: http.RequestOptions | string | URL,
      optionsOrCallback?: http.RequestOptions | HttpCallback,
      callbackOrVoid?: HttpCallback,
    ) => {
      let url: string | URL;
      let options: http.RequestOptions;
      let callback: (res: http.IncomingMessage) => void;
      if (typeof urlOrOptions === 'string' || urlOrOptions instanceof URL) {
        url = urlOrOptions as string | URL;
        options = optionsOrCallback as http.RequestOptions;
        callback = callbackOrVoid as HttpCallback;
        this.addTraceHeaders(options);
        return originalRequest(url, options, callback);
      }
      options = urlOrOptions as http.RequestOptions;
      callback = optionsOrCallback as HttpCallback;
      this.addTraceHeaders(options);
      return originalRequest(options, callback);
    };
    protocol.request = wrappedRequest;
  }

  traceApp(app: Application) {
    this.app = app;
    app.use(httpContext.middleware);
    app.use(this.middleware.bind(this));
  }

  shouldBeTraced(req: Request) {
    const contentType = req.headers['content-type'];
    return Boolean(!contentType || contentType.match(/^(text|application)\/(html|json)/i));
  }

  parseTraceState(tracestate = '') {
    const state: Record<string, string> = {};
    tracestate.split(',').forEach((keyVal) => {
      if (keyVal) {
        const [key, value] = keyVal.split('=');
        state[key.trim()] = value.trim();
      }
    });
    return state;
  }

  parseCookie(cookieName: string, req: Request) {
    const cookies = req.get('cookie');
    if (cookies) {
      const regex = new RegExp(`(?:^|;\\s*)${cookieName}=(.*?)(?:;|$)`);
      const match = cookies.match(regex);
      if (match) {
        return match[1];
      }
    }
  }

  middleware(req: Request, res: Response, next: NextFunction) {
    if (this.shouldBeTraced(req)) {
      const traceCookie = this.parseCookie(TRACE_COOKIE_NAME, req);
      const cookieParts = traceCookie ? traceCookie.split(':') : [];
      let [sessionId, traceId] = cookieParts;
      let doSaveFlag = cookieParts[2];
      if (!traceId || traceId.length !== TRACE_ID_LENGTH) {
        const traceparent = req.headers?.traceparent || '';
        const traceParts = traceparent.toString().split('-');
        if (traceParts.length === 4 && traceParts[1].length === TRACE_ID_LENGTH) {
          traceId = traceParts[1];
          doSaveFlag = traceParts[3];
        }
        if (!traceId) {
          traceId = this.makeTraceID();
        }
      }
      if (!sessionId) {
        sessionId = this.makeID(SESSION_ID_LENGTH);
      }
      res.setHeader(
        'set-cookie',
        `${TRACE_COOKIE_NAME}=${sessionId}::; path=/; max-age=${TRACE_COOKIE_DURATION_SEC}; samesite=strict`,
      );
      const tracestate = this.parseTraceState(req.headers.tracestate as string);
      const pluginTags = this.plugin.parseRequestTags(req, this.parseCookie);
      httpContext.set('trace', {
        traceId,
        sessionId,
        reqId: this.makeID(SPAN_ID_LENGTH),
        doSave: doSaveFlag || this.saveAllTraces || req.query.debug,
        tags: {
          ...tracestate,
          ...pluginTags,
        },
      });
      this.plugin.trace(
        (done) => {
          res.on('finish', () => {
            done();
          });
        },
        `${req.method} ${req.path}`,
        tracestate,
      );
    }
    next();
  }

  trace<T>(fn: (...args: Array<unknown>) => T, name = fn.name || 'anonymous', tags: Record<string, unknown> = {}): T {
    const currentTrace = this.getCurrentTrace();
    if (currentTrace) {
      Object.assign(tags, currentTrace.tags);
    }
    return this.plugin.trace(fn, name, tags) as T;
  }

  setRequestTag(key: string, value: string) {
    const trace = this.getCurrentTrace() || { tags: {} };
    trace.tags[key] = value;
  }

  getRequestTag(key: string) {
    const trace = this.getCurrentTrace() || {};
    return trace.tags[key];
  }

  getCurrentTrace(): Trace {
    return httpContext.get('trace');
  }

  getCurrentSpan(): Span | undefined {
    return this.plugin.getCurrentSpan();
  }

  makeID(length = SESSION_ID_LENGTH) {
    const id = crypto.randomBytes(Math.ceil(length / 2)).toString('hex');
    return id.slice(0, length);
  }

  makeTraceID() {
    const currentTimeNs = BigInt(nanoTime());
    const formattedTimestamp = currentTimeNs.toString(16).padStart(16, '0').substring(0, 16);
    return formattedTimestamp + this.makeID(SPAN_ID_LENGTH);
  }

  getCurrentTags() {
    let spanTags = this.plugin.getAllTags();
    let trace = httpContext.get('trace');
    if (trace || spanTags) {
      trace = trace || { tags: {} };
      spanTags = spanTags || {};
      const { sessionId, reqId } = trace;
      return {
        js: '1', // flag traces that were initiated from JS apps
        sessionId,
        reqId,
        ...trace.tags,
        ...spanTags,
      };
    }
  }

  formatTracestatePair(key: string, value: string) {
    key = key.toLowerCase().replace(/[^a-z0-9_\-*/]/g, '');
    value = value.replace(/[,=\s]/g, '');
    return value.length ? `${key}=${value}` : '';
  }

  addTraceHeaders(options: http.RequestOptions) {
    let trace = httpContext.get('trace');
    let tags = this.getCurrentTags();
    if (trace || tags) {
      trace = trace || {};
      tags = tags || {};
      const traceId = trace.traceId || this.makeTraceID();
      const spanId = this.makeID(SPAN_ID_LENGTH);
      let doSaveFlag: string | number = parseInt(trace.doSave);
      if (isNaN(doSaveFlag)) {
        doSaveFlag = trace.doSave || this.saveAllTraces ? '1' : '0';
      }
      const traceparent = `00-${traceId}-${spanId}-0${doSaveFlag}`;
      const tracestate: Array<string> = [];
      Object.keys(tags).forEach((key) => {
        if (tags[key] !== undefined) {
          const statePair = this.formatTracestatePair(key, tags[key]?.toString() || '');
          if (statePair) {
            tracestate.push(statePair);
          }
        }
      });
      options.headers = options.headers || {};
      options.headers.traceparent = traceparent;
      options.headers.tracestate = tracestate.join(',');
    }
  }

  logMixin(log?: unknown) {
    const tags = this.getCurrentTags();
    if (tags) {
      const trace = httpContext.get('trace') || {};
      if (this.isError(log)) {
        this.setSaveTrace(true);
      }
      return {
        traceId: trace.traceId,
        ...tags,
      };
    }
    return {};
  }

  isError(log?: unknown) {
    return log && typeof log === 'object' && ERROR_KEYS.find((key) => (log as Record<string, unknown>)[key]);
  }

  setSaveTrace(doSave: boolean) {
    const trace = httpContext.get('trace');
    if (trace) {
      trace.doSave = doSave;
    }
  }

  errorMiddleware(app = this.app) {
    app?.use((error: Error, req: Request, res: Response, next: NextFunction) => {
      this.setSaveTrace(true);
      error.name && this.setSpanTag('error.type', error.name);
      error.message && this.setSpanTag('error.msg', error.message);
      error.stack && this.setSpanTag('error.stack', error.stack);
      next(error);
    });
  }
}
