import type { Span as DDSpanScope, SpanContext, Tracer, TracerOptions } from 'dd-trace';
import type { Request } from 'express';
import type { NodeTracerPlugin, ParseCookie, TraceCallback } from '.';
import type { Span, Tags } from '@zg-rentals/trace-base';
import tracer from 'dd-trace';
import BaseTracer from '@zg-rentals/trace-base';

export interface TaggedSpanContext extends SpanContext {
  _tags: Tags;
}

// DataDog automatically adds these tags to each span,
// but we don't necessarily want to include them in every log
const DD_TAG_BLACKLIST: Record<string, true> = {
  _sample_rate: true,
  'service.name': true,
  service: true,
  env: true,
  version: true,
  'resource.name': true,
};

export class DatadogTracerPlugin implements NodeTracerPlugin {
  tracer: Tracer;

  constructor(options: TracerOptions) {
    if (options.runtimeMetrics === undefined) {
      options.runtimeMetrics = true;
    }
    this.tracer = tracer.init(options);
  }

  trace<T>(fn: TraceCallback<T>, name: string, tags: Record<string, unknown>): T {
    if (BaseTracer.isAsyncFunction(fn)) {
      return this.tracer.trace(name, (_, done) => {
        this.setTags(tags);
        return fn(done as (error?: Error) => unknown);
      });
    }
    return this.tracer.trace(name, () => {
      this.setTags(tags);
      return fn(() => this.endSpan());
    });
  }

  scopeToSpan(scope: DDSpanScope): Span {
    const context = scope.context() as TaggedSpanContext;

    return Object.assign(scope, {
      spanId: context.toSpanId(),
      traceId: context.toTraceId(),
      getTags: () => context._tags || {},
      setTags: (tags: Tags) => scope.addTags(tags),
      finish: scope.finish,
    }) as Span;
  }

  startSpan(name: string) {
    const scope = this.tracer.startSpan(name);
    return this.scopeToSpan(scope);
  }

  endSpan(span = this.getCurrentSpan()) {
    span?.finish();
  }

  getCurrentSpan(): Span | undefined {
    const scope = this.tracer.scope().active();
    if (scope) {
      return this.scopeToSpan(scope);
    }
    return;
  }

  setTags(tags: Tags) {
    const span = this.getCurrentSpan();
    if (span) {
      span.setTags(tags);
    }
  }

  getAllTags() {
    const span = this.getCurrentSpan();
    const spanTags = span?.getTags() || {};
    if (!spanTags.ddTraceID) {
      spanTags.ddTraceID = span?.traceId;
    }
    const filteredTags: Record<string, unknown> = {};
    let hasTags;
    Object.keys(spanTags).forEach((key) => {
      if (!DD_TAG_BLACKLIST[key]) {
        hasTags = true;
        filteredTags[key] = spanTags[key];
      }
    });
    return hasTags ? filteredTags : undefined;
  }

  parseRequestTags(req: Request, parseCookie: ParseCookie) {
    const tags: {
      ddTrcID?: string;
      ddSesID?: string;
    } = {};
    const ddTraceId = req.headers['x-datadog-trace-id'] as string | undefined;
    if (ddTraceId) {
      tags.ddTrcID = ddTraceId.split(', ')[0];
    }
    const ddCookie = parseCookie('_dd_s', req) || '';
    const [, ddCookieFragment = ''] = ddCookie.split('&id=');
    const [ddSessionId] = ddCookieFragment.split('&');
    if (ddSessionId) {
      tags.ddSesID = ddSessionId;
    }
    return tags;
  }
}
