/* requires jest to run
 */
export default class TestUtility {
  private output: string;
  private write: typeof process.stdout.write;

  constructor() {
    this.output = '';
    this.write = process.stdout.write;
  }

  getOutput = (): string => {
    return this.output;
  };

  mockStdout = (): void => {
    jest.spyOn(process.stdout, 'write').mockImplementation((data) => {
      this.output += data;
      return true;
    });
  };

  clearOutput = (): void => {
    this.output = '';
  };

  restoreStdout = (): void => {
    process.stdout.write = this.write;
  };
}
