export default class ShutdownListener {
  constructor(onShutdown: () => void, onSigInt: () => void = onShutdown) {
    console.log({
      msg: '[DEPRECATED]: ShutdownListener class has been deprecated. please use particles-js-notify instead.',
      level: 50,
    });

    process.on('SIGINT', () => {
      onSigInt();
    });

    process.on('message', ({ topic }) => {
      if (topic === 'TRIGGER_SHUTDOWN') {
        onShutdown();
      }
    });
  }
}
