import type { Logger } from '@zg-rentals/logger-base';
import type { GaugeOptions, IncrementOptions } from './baseMonitor';
import type { StatController } from './statController';

export interface Plugin {
  pluginName: string;
  logger?: Logger;
  stats?: StatController;
  increment?: (options: IncrementOptions) => void;
  gauge?: (options: GaugeOptions) => void;
  onInitialize(increment: (i: IncrementOptions) => void, gauge: (g: GaugeOptions) => void, logger?: Logger): void;
}

export interface MonitorPluginInterface {
  pluginName: string;
  logger?: Logger;
}

export abstract class MonitorPlugin {
  readonly pluginName: string;
  logger?: Logger;
  stats?: StatController;
  increment?: (options: IncrementOptions) => void;
  gauge?: (options: GaugeOptions) => void;

  abstract onInitialize(
    increment: (i: IncrementOptions) => void,
    gauge: (g: GaugeOptions) => void,
    logger?: Logger,
  ): void;

  constructor({ pluginName, logger }: MonitorPluginInterface) {
    this.pluginName = pluginName;
    this.logger = logger;
  }
}
