import type { NodeTracerPlugin, TraceCallback } from '.';
import type { Tags } from '@zg-rentals/trace-base';
import BaseTracer from '@zg-rentals/trace-base';

export class BaseSpan {
  tags: Record<string, unknown> = {};
  onFinish?: () => void;
  spanId = '';
  traceId = '';

  constructor(onFinish?: () => void) {
    this.onFinish = onFinish;
  }

  getTags() {
    return this.tags;
  }

  setTags(tags: Tags) {
    Object.assign(this.tags, tags);
  }

  finish() {
    if (this.onFinish) {
      this.onFinish();
    }
  }
}

export class BaseTracerPlugin implements NodeTracerPlugin {
  activeSpan?: BaseSpan;

  trace<T>(fn: TraceCallback<T>, name: string, tags: Record<string, unknown>) {
    const span = this.startSpan();
    const finish = span.finish.bind(span);
    Object.assign(span.tags, tags, { name });
    if (BaseTracer.isAsyncFunction(fn)) {
      return fn(finish);
    }
    const ret = fn(finish);
    if (ret instanceof Promise) {
      return ret.finally(finish);
    }
    finish();
    return ret;
  }

  setTags(tags: Tags) {
    if (this.activeSpan) {
      Object.assign(this.activeSpan.tags, tags);
    }
  }

  parseRequestTags() {
    return {};
  }

  startSpan() {
    this.activeSpan = new BaseSpan(() => {
      delete this.activeSpan;
    });
    return this.activeSpan;
  }

  getCurrentSpan() {
    return this.activeSpan;
  }

  getAllTags() {
    return this.activeSpan?.tags;
  }
}
