import { deprecateMessage } from './deprecate';

const prodEnvList = ['production', 'production-docker'];
export const isProd = () => {
  const zgEnv = process.env.ZG_ENV;

  if (!zgEnv) return false;

  return prodEnvList.includes(zgEnv);
};

const stageEnvList = ['comet-1'];
export const isStaging = () => {
  deprecateMessage('Please use the EnvrionmentUtil class from @zg-rentals/particles-js-utils/lib/server');

  const zgEnv = process.env.ZG_ENV;

  if (!zgEnv) return false;

  return stageEnvList.includes(zgEnv);
};
