import type { Logger } from '@zg-rentals/logger-base';
import { Utils } from '@zg-rentals/particles-js-utils';
import type express from 'express';
import os from 'os';
import readPkg from 'read-pkg';
import v8 from 'v8';

export const BAD_SHUTDOWN_TOKEN_MSG = 'invalid shutdown token provided.';
export const GOOD_STATUS_CODE = 200;
export const SHUTDOWN_STATUS_CODE = 555;

export interface onPingInterface {
  hasAccess: boolean;
  isProtected: boolean;
  response: { [key: string]: unknown };
}

export interface HealthcheckInterface {
  shutdownToken?: string;
  accessToken?: string;
  protectedQueryParams?: Array<string>;
  onPing?: ({ hasAccess, isProtected, response }: onPingInterface) => unknown;
  logger?: Logger;
  buildNumber: string | number;
  maxMemoryThresholdMb?: number;
}

export interface EnvironmentData {
  node_version: string;
  NODE_ENV: string;
  gitCommit: string;
  gitUsername: string;
  gitBranch: string;
  buildDate: string;
}

export interface ServerData {
  host: string;
  uptime: number;
  memory_used: NodeJS.MemoryUsage;
  memory_total: number;
  memory_free: number;
  average_load: Array<number>;
  heap: v8.HeapInfo;
}

export interface DependencyMap {
  [key: string]: string;
}

export interface PackageJson {
  name: string;
  alias?: string;
  version?: string;
  main?: string;
  module?: string;
  bin?: string | Record<string, string>;
  scripts?: Record<string, string>;
  sideEffects?: boolean;
  publishConfig?: Record<string, string>;
  private?: boolean;
  dependencies?: DependencyMap;
  devDependencies?: DependencyMap;
  peerDependencies?: DependencyMap;
  workspaces?: Array<string>;
}

export type ConfidentialData = EnvironmentData & ServerData & Pick<PackageJson, 'dependencies' | 'devDependencies'>;

export class Healthcheck {
  shutdownToken?: string;
  protectedQueryParams: Array<string>;
  accessToken?: string;
  isShuttingDown: boolean;
  logger?: Logger;
  packageJson: readPkg.NormalizedPackageJson;
  buildNumber: number;
  onPing?: ({ hasAccess, isProtected, response }: onPingInterface) => unknown;
  maxMemoryThresholdMb: number;

  constructor({
    logger,
    shutdownToken,
    accessToken,
    buildNumber = 0,
    onPing,
    protectedQueryParams = ['runscope', 'sonar', 'liveliness', 'site_liveliness'],
    maxMemoryThresholdMb = 5_120,
  }: HealthcheckInterface) {
    this.logger = logger;
    this.accessToken = accessToken;
    this.shutdownToken = shutdownToken;
    this.protectedQueryParams = protectedQueryParams;
    this.isShuttingDown = false;
    this.buildNumber = Number(buildNumber);
    this.setupPm2GracefulExit();
    this.packageJson = readPkg.sync();
    this.onPing = onPing;
    this.maxMemoryThresholdMb = maxMemoryThresholdMb;

    this.logger?.info('instantiating healthcheck');
  }

  setupPm2GracefulExit = (): void => {
    if (Utils.isProd() && process && process.send) {
      this.logger?.debug('setting up pm2 graceful exit');

      process.on('message', (message: { topic?: string; [key: string]: unknown }) => {
        this.logger?.warn(message, 'healthcheck received a process message');

        const { topic } = message;

        if (topic === 'TRIGGER_SHUTDOWN') {
          this.isShuttingDown = true;
        }
      });
    }
  };

  bytesToMb(bytes: number): number {
    if (bytes === 0) return 0;

    return bytes / 1024 / 1024;
  }

  getPackageJsonData(): Pick<PackageJson, 'dependencies' | 'devDependencies'> {
    const data: { dependencies: DependencyMap; devDependencies: DependencyMap } = {
      dependencies: {},
      devDependencies: {},
    };

    const { dependencies = {}, devDependencies = {} } = this.packageJson;

    for (const dependency in dependencies) {
      if (dependency.startsWith('@zg-rentals/')) {
        data.dependencies[dependency] = dependencies[dependency];
      }
    }
    for (const dependency in devDependencies) {
      if (dependency.startsWith('@zg-rentals/')) {
        data.devDependencies[dependency] = devDependencies[dependency];
      }
    }

    return data;
  }

  getEnvironmentData(): EnvironmentData {
    return {
      NODE_ENV: process.env.NODE_ENV || 'development',
      node_version: process.versions.node,
      gitCommit: process.env.GIT_COMMIT || '',
      gitUsername: process.env.GIT_COMMIT_AUTHOR || '',
      gitBranch: process.env.BRANCH_NAME || '',
      buildDate: process.env.BUILD_TIME || '',
    };
  }

  getServerData(): ServerData {
    return {
      host: os.hostname(),
      uptime: process.uptime(),
      memory_used: process.memoryUsage(),
      memory_total: os.totalmem(),
      memory_free: os.freemem(),
      average_load: os.loadavg(),
      heap: v8.getHeapStatistics(),
    };
  }

  hasAccess = (req: express.Request): boolean => {
    if (!this.accessToken) return false;

    if (req.query.accessToken === this.accessToken) return true;

    return false;
  };

  isMemoryOverloaded(memoryUsage: NodeJS.MemoryUsage): boolean {
    if (!memoryUsage || !memoryUsage.rss) {
      return false;
    }

    const mb = this.bytesToMb(memoryUsage.rss);

    if (mb >= Number(this.maxMemoryThresholdMb)) {
      return true;
    }

    return false;
  }

  aggregateConfidentialData = (req: express.Request): ConfidentialData | undefined => {
    const data = {
      ...this.getPackageJsonData(),
      ...this.getEnvironmentData(),
      ...this.getServerData(),
    };

    if (this.isMemoryOverloaded(data.memory_used)) {
      this.isShuttingDown = true;
    }

    if (!this.hasAccess(req)) {
      return;
    }

    return data;
  };

  generateBaseResponse(): { name: string; buildNumber: number } {
    return {
      name: this.packageJson.name,
      buildNumber: this.buildNumber,
    };
  }

  shouldIgnoreShutdown(req: express.Request): boolean {
    if (typeof req.query.mode === 'string' && this.protectedQueryParams.includes(req.query.mode)) {
      return true;
    }

    return false;
  }

  getStatusCode = (req: express.Request): number => {
    if (this.isShuttingDown) {
      if (this.shouldIgnoreShutdown(req)) {
        this.logger?.warn(`app is ignoring shutdown mode, returning status code=${GOOD_STATUS_CODE}`);
        return GOOD_STATUS_CODE;
      }
      this.logger?.warn(`app is in shutdown mode, returning status code=${SHUTDOWN_STATUS_CODE}`);

      return SHUTDOWN_STATUS_CODE;
    } else {
      return GOOD_STATUS_CODE;
    }
  };

  check = (req: express.Request, res: express.Response): void => {
    try {
      const confidentialData = this.aggregateConfidentialData(req);
      const baseResponse = this.generateBaseResponse();
      const status = this.getStatusCode(req);

      const response = {
        ...confidentialData,
        ...baseResponse,
      };

      if (this.onPing) {
        res.status(status).json(
          this.onPing({
            hasAccess: this.hasAccess(req),
            isProtected: this.shouldIgnoreShutdown(req),
            response,
          }),
        );
      } else {
        res.status(status).json(response);
      }
    } catch (err: any) {
      res.status(500).json({
        error: err.message,
        success: false,
      });
    }
  };

  shutdown = (req: express.Request, res: express.Response): void => {
    const reqShutdownToken = req.query.secretToken;
    const isNotEmpty = reqShutdownToken !== '';
    const isDefined = typeof reqShutdownToken !== 'undefined' && typeof this.shutdownToken !== 'undefined';

    if (isDefined && isNotEmpty && reqShutdownToken === this.shutdownToken) {
      this.isShuttingDown = true;
      this.logger?.warn('successful shutdownToken received, putting app in shutdown mode');

      res.status(200).json({
        success: true,
        statusCode: SHUTDOWN_STATUS_CODE,
      });
    } else {
      this.logger?.warn('incorrect or missing shutdown token');

      res.status(500).json({
        success: false,
        error: BAD_SHUTDOWN_TOKEN_MSG,
      });
    }
  };
}
