import type { MonitorPluginInterface } from '../../plugin';
import { MonitorPlugin } from '../../plugin';
import { StatController } from '../../statController';

export class PlainPlugin extends MonitorPlugin {
  constructor(args: Omit<MonitorPluginInterface, 'pluginName'> = {}) {
    super({
      ...args,
      pluginName: 'PlainPlugin',
    });

    this.stats = new StatController({
      limit: 10,
    });
  }

  onError({ error }: { error: Error }) {
    this.logger && this.logger.error(error);

    return Promise.resolve();
  }

  onInitialize() {}
}
