import type { MetricFnInterface, MetricOptions } from '@zg-rentals/monitor-base';
import type { Logger } from '@zg-rentals/logger-base';
import type { MetricAlert } from '@zg-rentals/pontoon';
import type { Request, Response } from 'express';
import { MonitorReporter, StatController } from '@zg-rentals/monitor-base';
import { Pontoon } from '@zg-rentals/pontoon';
import { NodeUtil } from '@zg-rentals/particles-js-utils';
import onHeaders from 'on-headers';

export interface NodeMonitorPontoonReporterArgs {
  api: string;
  apiKey: string;
  logger?: Logger;
  webappName: string;
  propertiesFilePath?: string;
  processManagerId?: string;
  sampleRate?: number;
}

export interface PontoonMetricFnInterface extends MetricFnInterface {
  name: string;
  amount: number;
  options?: MetricOptions;
}

const DEFAULT_PROPERTIES_FILE_PATH = '/hotpads/config/server.properties';

export class NodeMonitorPontoonReporter extends MonitorReporter {
  pontoon: Pontoon;
  intervals: Record<string, NodeJS.Timeout> = {};
  logger?: Logger;
  webappName: string;
  serverName: string;
  stats: StatController;
  registeredAlerts: Record<string, boolean> = {};

  constructor({
    api,
    apiKey,
    webappName,
    logger,
    propertiesFilePath = DEFAULT_PROPERTIES_FILE_PATH,
    processManagerId = process.env.pm_id,
    sampleRate,
  }: NodeMonitorPontoonReporterArgs) {
    super({
      logger,
      reporterName: 'NodePontoonReporter',
      sampleRate,
    });

    const { serverProperties, envProperties } = new NodeUtil.EnvironmentUtil(propertiesFilePath, {
      appName: webappName,
    });
    const serverName = serverProperties.name || '';
    const containerIdMatch = envProperties.hostname.match(/-\d+$/);
    const containerId = containerIdMatch ? containerIdMatch[0] : '';
    const processManagerIdSuffix = processManagerId ? `-${processManagerId}` : '';

    this.webappName = webappName;
    this.serverName = `${serverName}${containerId}${processManagerIdSuffix}`;

    if (api === undefined || apiKey === undefined || webappName === undefined) {
      const errorMessage = `${this.reporterName} reporter missing required arg(s)`;
      this.logger?.error(errorMessage);
      throw new Error(errorMessage);
    }

    this.pontoon = new Pontoon({ api, apiKey });

    this.stats = new StatController({
      limit: 100,
      onRecord: (stat) => this.pontoon.addStatUrl({ stat, serviceName: webappName, api }),
    });
  }

  onInitialize(logger?: Logger) {
    this.logger = this.logger || logger;
  }

  shouldSave(options: Record<string, unknown> = {}) {
    const random = Math.random();
    return random <= this.sampleRate && (typeof options.sampleRate !== 'number' || random <= options.sampleRate);
  }

  async onIncrement({ name, amount = 1, options }: PontoonMetricFnInterface) {
    if (options?.alert) {
      this.registerAlert({
        metricName: name,
        metricType: 'COUNT',
        ...options.alert,
      } as MetricAlert);
    }
    if (this.shouldSave(options)) {
      this.logger?.info(`incrementing ${name} by ${amount}`);
      await this.pontoon
        .sendCounts({
          serviceName: this.webappName,
          serverName: this.serverName,
          counts: {
            [Date.now()]: {
              [name]: amount,
            },
          },
        })
        .then(() => {
          this.stats.record({ name, count: amount });
          return;
        })
        .catch((e) => this.onError({ error: e }));
    }
  }

  async onGauge({ name, amount, options }: PontoonMetricFnInterface): Promise<void> {
    if (options?.alert) {
      this.registerAlert({
        metricName: name,
        metricType: 'GAUGE',
        ...options.alert,
      } as MetricAlert);
    }
    if (this.shouldSave(options)) {
      this.logger?.info(`${name}, ${amount}`);
      await this.pontoon
        .sendGauges({
          serviceName: this.webappName,
          serverName: this.serverName,
          gauges: {
            [name]: [[Date.now(), amount]],
          },
        })
        .then(() => this.stats.record({ name, count: amount }))
        .catch((e) => this.onError({ error: e }));
    }
  }

  async onError({ error }: { error: Error }) {
    this.logger?.error(error);
  }

  registerAlert(alertOptions: MetricAlert) {
    if (this.registeredAlerts[alertOptions.metricName]) {
      return;
    }
    if (!alertOptions.periodMs || !alertOptions.alertFrequencyMinutes) {
      this.logger?.error('could not register alert, missing frequency information');
      return;
    }

    if (alertOptions.minThreshold === undefined && alertOptions.maxThreshold === undefined) {
      this.logger?.error('could not register alert, no min or max threshold');
      return;
    }

    this.logger?.debug(`registering metric alert: ${alertOptions.metricType} ${alertOptions.metricName}`);
    this.registeredAlerts[alertOptions.metricName] = true;
    return this.pontoon.registerAlert(alertOptions);
  }

  onRequest(req: Request, res: Response) {
    this.onIncrement({ name: 'request', amount: 1 });
    this.onIncrement({ name: `request method ${req.method}`, amount: 1 });
    onHeaders(res, () => {
      const statusCode = Number.isFinite(res.statusCode) ? res.statusCode : 'UNKNOWN';
      this.onIncrement({ name: `response status code ${statusCode}`, amount: 1 });
    });
  }
}
