import type { Span, Tags } from '@zg-rentals/trace-base';
import type { Request } from 'express';

export * from './datadog';
export * from './base';

// Matches any stringified function that expects to be passed an argument
// YES: (arg) => { ... }
// YES: arg => { ... }
// YES: function hello ( goodbye ) { ... }
// NO: ( ) => { ... }
// NO: function test() { ... }
// NO: function( ) { ... }
export const ASYNC_FN_MATCHER = /^(\(?\s*\w+\s*\)?\s*=>|function(\s+\w*\s*)?\(\s*\w)/;

export type TraceCallback<T> = (done: (error?: Error) => unknown) => T;

export type TraceFunc<T = unknown> = (fn: TraceCallback<T>, name: string, tags: Record<string, unknown>) => T;

export type ParseCookie = (cookieName: string, req: Request) => string | void;

export interface NodeTracerPlugin {
  trace: TraceFunc;
  setTags: (tags: Tags) => void;
  parseRequestTags: (req: Request, parseCookie: ParseCookie) => Record<string, unknown>;
  startSpan: (name: string) => Span;
  getCurrentSpan: () => Span | undefined;
  getAllTags: () => Record<string, unknown> | undefined;
}
