import os from 'os';

import { properties } from '../helpers/propertiesReader';

type environment = 'sherman' | 'comet1' | 'production' | 'local' | 'ttest' | 'ztest' | 'other' | 'jenkins';

export interface ServerProperties {
  name: string;
  type: string;
  publicIp: string;
  privateIp: string;
}

export interface EnvProperties {
  server: ServerProperties;
  hostname: string;
  id: string;
  environment: environment;
}

export interface EnvironmentUtilInterface {
  appName: string;
}

export class EnvironmentUtil {
  public serverProperties: ServerProperties;
  public envProperties: EnvProperties;

  constructor(propertiesFilePath: string, { appName }: EnvironmentUtilInterface) {
    try {
      const readProperties = properties(propertiesFilePath);

      const sp: ServerProperties = {
        name: String(readProperties.get('server.name')),
        type: String(readProperties.get('server.type')),
        publicIp: String(readProperties.get('server.publicIp')),
        privateIp: String(readProperties.get('server.privateIp')),
      };

      this.serverProperties = sp;
    } catch (err) {
      this.serverProperties = {
        name: os.hostname(),
        type: appName,
        publicIp: '',
        privateIp: '',
      };
    }

    const hostname = os.hostname();

    this.envProperties = {
      server: this.serverProperties,
      hostname,
      id: `${this.serverProperties.name}-${hostname}`,
      environment: this.guessEnvironment(),
    };
  }

  guessEnvironment(): environment {
    const { name } = this.serverProperties;

    if (name.includes('production')) {
      return 'production';
    } else if (name.includes('comet')) {
      return 'comet1';
    } else if (name.includes('ttest')) {
      return 'ttest';
    } else if (name.includes('ztest')) {
      return 'ztest';
    } else if (name.includes('sherman')) {
      return 'sherman';
    } else if (name.includes('jenkins')) {
      return 'jenkins';
    } else {
      return 'other';
    }
  }
  // any non-local server that is not production
  isStaging() {
    const { environment } = this.envProperties;

    return ['comet1', 'ttest', 'ztest', 'sherman'].indexOf(environment) > -1;
  }

  // production servers
  isProduction() {
    const { environment } = this.envProperties;

    return environment === 'production';
  }

  isSherman() {
    const { environment } = this.envProperties;

    return environment === 'sherman';
  }

  isJenkins() {
    const { environment } = this.envProperties;

    return environment === 'jenkins';
  }
}

export default EnvironmentUtil;
