import type { Logger } from '@zg-rentals/logger-base';
import type { LogLevel } from '@zg-rentals/monitor-base';
import type { Request } from 'express';
import { LogReporter } from '@zg-rentals/monitor-base';

export class NodeLogReporter extends LogReporter {
  enableRequestLogs: boolean;

  constructor({
    logger,
    level = 'warn',
    enableRequestLogs = true,
    sampleRate,
  }: {
    logger?: Logger;
    level?: LogLevel;
    enableRequestLogs?: boolean;
    sampleRate?: number;
  } = {}) {
    super({
      logger,
      level,
      sampleRate,
    });

    this.enableRequestLogs = enableRequestLogs;
  }

  onRequest(req: Request) {
    if (this.enableRequestLogs && this.log && (this.sampleRate === 1 || Math.random() <= this.sampleRate)) {
      this.log(`${req.method}: ${req.url}`);
    }

    return Promise.resolve();
  }
}
