import type pino from 'pino';

export interface fakeLogger {
  trace: () => void;
  debug: () => void;
  info: () => void;
  warn: () => void;
  error: () => void;
  fatal?: () => void;
  child?: () => any;
}

export type ParticlesLogger = fakeLogger | pino.Logger;

export function safeCreateLogger(log?: any, name?: string): pino.Logger | fakeLogger {
  if (log) {
    return log.child({ name });
  } else {
    const logger = {
      child: function () {
        return this;
      },
      trace: (...args: Array<any>) => {
        console.log(args);
      },
      debug: (...args: Array<any>) => {
        console.log(args);
      },
      info: (...args: Array<any>) => {
        console.log(args);
      },
      warn: (...args: Array<any>) => {
        console.log(args);
      },
      error: (...args: Array<any>) => {
        console.log(args);
      },
      fatal: (...args: Array<any>) => {
        console.log(args);
      },
    };
    return logger;
  }
}

export function safeUseLogger(
  logger: ParticlesLogger,
  log: ParticlesLogger,
  backupName: string,
): pino.Logger | fakeLogger {
  // try to use a created logger
  if (logger) {
    return logger;
  } else {
    return safeCreateLogger(log, backupName);
  }
}
